import React, { Component } from 'react';
import "./Hub.css";
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Dropdown from 'react-bootstrap/Dropdown';
import ToggleButton from 'react-bootstrap/ToggleButton';
import {getDateISO} from "../helpers/calendar";
import DatePicker, { registerLocale, setDefaultLocale } from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { sv } from 'date-fns/locale';
//import { format, formatDistance, formatRelative, subDays } from 'date-fns';
import CustomInput from "../components/CustomInput";
import PropTypes from 'prop-types';

const baseapi = 'http://' + window.location.hostname + ':1337';

const url = baseapi + "/settings";
const urledit = baseapi + "/editsettings";
const control = baseapi + "/hourcontrol";

/*
import calendar, {
  isDate,
  isSameDay,
  isSameMonth,
  getDateISO,
  getNextMonth,
  getPreviousMonth,
  WEEK_DAYS,
  CALENDAR_MONTHS
} from "../helpers/calendar";*/

CustomInput.propTypes = {
  onClick: PropTypes.func,
  value: PropTypes.string,
  placeholderText: PropTypes.object
};

registerLocale('sv', sv);
console.log(sv);

export default class Settings extends Component {
  _isMounted = false;

  constructor(props) {
    super(props);
    console.log(props);

    registerLocale("sv", sv);
    setDefaultLocale("sv");
    this.state = {
      dsmon: 0,
      currency: "currency",
      area: "area",
      percenton: 0,
      percent: 0,
      lat: "Latitud",
      long: "Longitud",
      awayto: "",
      awayfrom: "",
      startDate: new Date(),
      toDate: new Date(),
      test: getDateISO("2019-07-23"),
    }
    this.handleAreaChange = this.handleAreaChange.bind(this);
    this.handlePercentChange = this.handlePercentChange.bind(this);
    this.handleCurrencyChange = this.handleCurrencyChange.bind(this);
    this.handleDsmChange = this.handleDsmChange.bind(this);
    this.handleFromChange = this.handleFromChange.bind(this);
    this.handleToChange = this.handleToChange.bind(this);
  }

  componentDidMount() {
    this._isMounted = true;
    fetch(url)
    .then(results => {
      //console.log(results);
      return results.json();
    })
    .then(data => {
      // eslint-disable-next-line
      if (this._isMounted) {
        console.log(data);
        this.setState({area: data.area});
        this.setState({currency: data.currency});
        this.setState({lat: data.lat});
        this.setState({long: data.long});
        this.setState({percent: data.percent});
        this.setState({percenton: data.percenton});
        this.setState({dsmon: data.dsmon});
        let from = data.awayfrom !== null ? data.awayfrom : this.state.awayfrom;
        let to = data.awayfrom !== null ? data.awayto : this.state.awayto;
        this.setState({awayfrom: from});
        this.setState({awayto: to});
        let start = data.awayfrom !== null ? new Date(data.awayfrom) : new Date();
        let cont = data.awayfrom !== null ? new Date(data.awayfrom) : new Date();
        this.setState({startDate: start});
        this.setState({toDate: cont});
      }
    })
    .catch(err => {
      console.log(err);
      if (err.name === 'AbortError') return;
      throw err;
    });
  }

  componentWillUnmount() {
    this._isMounted = false;
  }


  handleAreaChange = (event) => {
    console.log('Area:' + event);
    this.updateSettings("area", event);
  }

  handlePercentChange = (event) => {
    console.log('Percent:' + event);
    this.updateSettings("percent", event);
  }

  handleCurrencyChange = (event) => {
      console.log("Currency:", event);
      this.updateSettings("currency", event);
  }

  getDsmText = (what) => {
    if (what !== undefined) {
        return what === 1 ? "Elnätsbolag styr" : "Elnätsbolag styr ej";
    }
  }

  getModeText = (what) => {
    if (what !== undefined) {
        return what === 1 ? "Du styr enligt elpris" : "Du styr ej enligt elpris";
    }
  }

  getModeVariant = (what) => {
      //console.log(what, typeof(what));
    if (what !== undefined) {
      return what === 1 ? "success" : "danger";
    }
  }

  handleControlChange = (what, event) => {
      console.log(what, event);
    event.preventDefault();
    if (what !== undefined) {
      let whatNow = what === 1 ? 0 : 1;
      console.log("Det var " + what + " Ska nu vara: ", whatNow)
      this.updateSettings("percenton", whatNow);
      fetch(control)
      .then( results => {
          return results.json();
      })
      .then( data => {
          console.log(data);
          this.setState({percenton: whatNow});
      })
      .catch(err => {
        console.log(err);
        if (err.name === 'AbortError') return;
        throw err;
      });
    }
  }

  handleDsmChange = (what, event) => {
    console.log("Dsm: ", what, event);
    event.preventDefault();
    if (what !== undefined) {
      let whatNow = what === 1 ? 0 : 1;
      console.log("Det var " + what + " Ska nu vara: ", whatNow)
      this.updateSettings("dsmon", whatNow);
      fetch(control)
      .then( results => {
          return results.json();
      })
      .then( data => {
          console.log(data);
          this.setState({dsmon: whatNow});
      })
      .catch(err => {
        console.log(err);
        if (err.name === 'AbortError') return;
        throw err;
      });
    }
  }

  handleFromChange = (event) => {
      console.log("From:", getDateISO(event));
      this.updateSettings("awayfrom", getDateISO(event));
      let dateval = new Date(getDateISO(event));
      this.setState({startDate: dateval});
  }

  handleToChange = (event) => {
      console.log("To:", getDateISO(event));
      this.updateSettings("awayto", getDateISO(event));
      let dateval = new Date(getDateISO(event));
      this.setState({toDate: dateval});
  }

  updateSettings = (col, val) => {
    console.log(col, val);
    fetch(urledit, {
      method: 'POST',
      body: 'column=' + col + '&value=' + val+ '&id=' + 1,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
    .then(res => {
      console.log(res);
      if (res.status === 200) {
        console.log("Uppdaterat");
        this.setState({
          [col]: val
        });
        console.log(this.state);
      } else {
        const error = new Error(res.error);
        throw error;
      }
    })
    .catch(err => {
      console.error(err);
    });
  }

  render() {
    let percenttext = <span><span className='redtext'>{ this.state.percent }</span> &#37;</span>;
    let areatext = <span>Område <span className='redtext'>{ this.state.area }</span></span>;
    let currencytext = <span>Valuta <span className='redtext'>{ this.state.currency }</span></span>;
    let lattext = <span>Latitud <span className='redtext'>{ this.state.lat }</span></span>;
    let longtext = <span>Longitud <span className='redtext'>{ this.state.long }</span></span>;
    let fromtext = <span><span className='twofive'>Bortrest från: </span><span className='redtext'>{ this.state.awayfrom }</span></span>;
    let totext = <span><span className='twofive'>Bortrest till: </span><span className='redtext'>{ this.state.awayto }</span></span>;

    return (
      <div className="hub-page">
        <h1>Inställningar</h1>
        <div className="d-flex flex-column">
          <div className="dropContainer">
            <ToggleButton type="checkbox" className="settings-toggle" onClick={this.handleControlChange.bind(this, this.state.percenton)} variant={this.getModeVariant(this.state.percenton)} >
              {this.getModeText(this.state.percenton)}
            </ToggleButton>
          </div>
          <div className="dropContainer">
            <ToggleButton type="checkbox" className="settings-toggle" onClick={this.handleDsmChange.bind(this, this.state.dsmon)} variant={this.getModeVariant(this.state.dsmon)} >
              {this.getDsmText(this.state.dsmon)}
            </ToggleButton>
          </div>
        </div>
        <DropdownButton id="dropdown-area" onSelect={this.handleAreaChange} title={areatext}>
            <Dropdown.Item eventKey="SE1">SE1</Dropdown.Item>
            <Dropdown.Item eventKey="SE2">SE2</Dropdown.Item>
            <Dropdown.Item eventKey="SE3">SE3</Dropdown.Item>
            <Dropdown.Item eventKey="SE4">SE4</Dropdown.Item>
            <Dropdown.Item eventKey="SP1">SP1</Dropdown.Item>
            <Dropdown.Item eventKey="DK1">DK1</Dropdown.Item>
            <Dropdown.Item eventKey="EE">EE</Dropdown.Item>
            <Dropdown.Item eventKey="FI">FI</Dropdown.Item>
            <Dropdown.Item eventKey="LT">LT</Dropdown.Item>
            <Dropdown.Item eventKey="LV">LV</Dropdown.Item>
            <Dropdown.Item eventKey="NO1">NO1</Dropdown.Item>
            <Dropdown.Item eventKey="NO2">NO2</Dropdown.Item>
            <Dropdown.Item eventKey="NO3">NO3</Dropdown.Item>
            <Dropdown.Item eventKey="NO4">NO4</Dropdown.Item>
            <Dropdown.Item eventKey="NO5">NO5</Dropdown.Item>
        </DropdownButton>
        <DropdownButton id="dropdown-currency" onSelect={this.handleCurrencyChange} title={currencytext}>
            <Dropdown.Item eventKey="SEK">SEK</Dropdown.Item>
            <Dropdown.Item eventKey="DKK">DKK</Dropdown.Item>
            <Dropdown.Item eventKey="NOK">NOK</Dropdown.Item>
            <Dropdown.Item eventKey="EUR">EUR</Dropdown.Item>
        </DropdownButton>
        <DropdownButton id="dropdown-percent" onSelect={this.handlePercentChange} title={percenttext}>
            <Dropdown.Item eventKey="0">0</Dropdown.Item>
            <Dropdown.Item eventKey="1">1</Dropdown.Item>
            <Dropdown.Item eventKey="2">2</Dropdown.Item>
            <Dropdown.Item eventKey="3">3</Dropdown.Item>
            <Dropdown.Item eventKey="4">4</Dropdown.Item>
            <Dropdown.Item eventKey="5">5</Dropdown.Item>
            <Dropdown.Item eventKey="6">6</Dropdown.Item>
            <Dropdown.Item eventKey="7">7</Dropdown.Item>
            <Dropdown.Item eventKey="8">8</Dropdown.Item>
            <Dropdown.Item eventKey="9">9</Dropdown.Item>
            <Dropdown.Item eventKey="10">10</Dropdown.Item>
            <Dropdown.Item eventKey="11">11</Dropdown.Item>
            <Dropdown.Item eventKey="12">12</Dropdown.Item>
            <Dropdown.Item eventKey="13">13</Dropdown.Item>
            <Dropdown.Item eventKey="14">14</Dropdown.Item>
            <Dropdown.Item eventKey="15">15</Dropdown.Item>
            <Dropdown.Item eventKey="16">16</Dropdown.Item>
            <Dropdown.Item eventKey="17">17</Dropdown.Item>
            <Dropdown.Item eventKey="18">18</Dropdown.Item>
            <Dropdown.Item eventKey="19">19</Dropdown.Item>
            <Dropdown.Item eventKey="20">20</Dropdown.Item>
        </DropdownButton>
        <Button variant="light" size="lg" block><Link to="/settings" className="buttonstyle">{lattext}</Link></Button>
        <Button variant="light" size="lg" block><Link to="/settings" className="buttonstyle">{longtext}</Link></Button>
        <div className="btn-light btn-block">
          <DatePicker
            customInput={<CustomInput placeholderText={fromtext} />}
            selected={this.state.startDate}
            dateFormat="yyyy-MM-dd"
            locale="sv"
            className=".btn-primary datefrom"
            calendarClassName="datefrom"
            showWeekNumbers
            minDate={new Date()}
            onChange={this.handleFromChange}
          /></div>
          <div className="btn-light btn-block">
          <DatePicker
            customInput={<CustomInput placeholderText={totext} />}
            selected={this.state.toDate}
            dateFormat="yyyy-MM-dd"
            locale="sv"
            className=".btn-primary dateto"
            calendarClassName="dateto"
            minDate={new Date(this.state.awayfrom)}
            showWeekNumbers
            onChange={this.handleToChange}
          />
        </div>
        <Button variant="light" size="lg" block><Link to="/zones" className="buttonstyle">Dina zoner</Link></Button>
      </div>
    );
  }
}
